.container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0px 30px;
}

.todos {
    padding: 30px;
    width: 400px;
    min-height: 500px;
    max-height: 800px;
    overflow-y: auto;
    border: 1px solid #2b5797;
    border-radius: 35px;
    display: flex;
    background-color: white;
    flex-direction: column;
    justify-content: space-between;
}

.todos h3 {
    margin-bottom: 30px;
    color: #2b5797;
}

.input-group {
    display: flex;
    height: 40px;
    justify-content: space-between;
    align-items: center;
}

.input-group button {
    width: 15%;
    height: 100%;
    border-radius: 20px;
    border: 1px solid #2b5797;
    background: #fff;
    color: #2b5797;
    font-size: 18px;
    cursor: pointer;
}

.input-group button:hover {
    border: none;
    background: #2b5797;
    color: #fff;
}

.input-group button:focus {
    outline: none;
}

.input-group input {
    width: 75%;
    border-radius: 20px;
    border: 1px solid #c1c0c0;
    color: #2b5797;
    font-size: 18px;
    padding: 7px 15px;
}

.input-group input:focus {
    outline: none;
}

.shadow {
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.divider {
    margin: 30px auto;
    border: 0.5px solid #2b5797;
    width: 200px;
}

.task-group {
    max-height: 300px;
    overflow-y: auto;
}

.task {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.task span {
    color: #a50000;
    font-size: 22px;
    cursor: pointer;
}

.task span:hover {
    color: red;
}

/*Checkboxes styles*/
input[type="checkbox"] {
    display: none;
}

input[type="checkbox"] + label {
    display: block;
    position: relative;
    padding-left: 35px;
    color: #2b5797;
    font-size: 18px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

input[type="checkbox"] + label:last-child {
    margin-bottom: 0;
}

input[type="checkbox"] + label:before {
    content: '';
    display: block;
    width: 20px;
    height: 20px;
    border: 1px solid #2b5797;
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: 0;
    opacity: .6;
    -webkit-transition: all .12s, border-color .08s;
    transition: all .12s, border-color .08s;
}

input[type="checkbox"]:checked + label:before {
    width: 10px;
    top: -5px;
    left: 5px;
    border-radius: 0;
    opacity: 1;
    border-top-color: transparent;
    border-left-color: transparent;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

.actions {
    display: flex;
    justify-content: space-around;
}

.actions button {
    margin-top: 30px;
    height: 100%;
    border-radius: 20px;
    border: 1px solid #2b5797;
    background: #fff;
    color: #2b5797;
    font-size: 18px;
    cursor: pointer;
    padding: 5px 20px;
}

.actions button.active {
    background: #2b5797;
    color: #ffffff;
}

.actions button:focus {
    outline: none;
}

::-webkit-scrollbar {
    width: 0;
}